import { Component, OnInit } from '@angular/core';
import { Contacts } from '../../models/contacts';
import { AuthService } from '../../services/auth.service';
import * as $ from 'jquery';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  public contacts = [];


  constructor(private _AuthService: AuthService) {
    this.acordeon();
   }

  ngOnInit() {
    this.getCommite();
  }

   acordeon(){
    $(function () {
      $(document).on('click', ".contacts_header", function () {
        var header = $(this);
        var content = $(this).siblings();
        var close = content.parents().siblings().find('.contacts_content');
        var close_header = header.parents().siblings().find('.contacts_header');

        if (header.hasClass('open')) {
          header.removeClass('open');
          content.slideUp(250).removeClass('open');
        } else {
          header.addClass('open');
          content.slideDown(250).addClass('open');
        }

        close_header.each(function (item, value) {
          value.classList.remove('open');
        });

        close.each(function (item, value) {
          value.classList.remove('open');
        });

        close.slideUp(250);

      });
    });
  }

  getCommite(){
    let token = localStorage.getItem('token');
    this._AuthService.getStandards(token).subscribe(
      res =>{

        if (res.result) {
          if (res.data[0] !== undefined) {
        let search = res.data[0].opportunity.office.name.toLowerCase();
        let _filter = Contacts.filter((contact) => {
          return contact.commite.includes(search);
        });
            this.contacts = (_filter.length <= 0) ? [] : _filter[0].data;       
          }
        }
        
      },
      error => {

      }
    )
  }

}



